@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.fade-enter {
  opacity: 0.01;
}

.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in
}

.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-out;
  transition: opacity 300ms ease-out
}

.transition-group {
  position: relative;
}

.route-section {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
.ant-menu-horizontal {
    border-bottom: none !important;
    font-size: 20px !important;
}
.ant-menu {
    padding: 0px 15px !important;
}
.ant-menu-item, .ant-menu-item-selected {
    border-bottom: none !important;
}
.ant-menu-item > a {
    color: black;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out
}
.ant-menu-item > a:hover {
    color: '#8a8caf' !important
}
.container {
    text-align: center;
    padding: 5px 15px; 
}
.avatar {
    margin-top: 40px;
    display: flex;
    justify-content: center;
}
.details, .name, .main-details {
    color: #FFF;
}
.rounded-circle {
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.2), 0 2px 10px 0 rgba(0,0,0,.12);
}

.main-background {
    background: url(/web101/static/media/web-main-background.a78c3c76.jpeg) no-repeat center center;
    -webkit-filter: opacity(0.9) brightness(1.2);
            filter: opacity(0.9) brightness(1.2);
    background-size: cover;
    width: 100%;
    min-height: 80vh;
    height: 60%;
    padding-top: 51px; 
    padding-bottom: 40px;
  }
  
.section {
    width: 100%;
    height: 60vh;
    background-color: rgba(0, 250, 100, 0.6);
    display: flex;
    justify-content: center;
}
.headline {
    text-align: center;
    align-self: center;
    display: flex;
    flex-direction: row;
}
.I-m {
    padding-right: 10px;
}
.in {
    display: inline;
}
.out {
    display: none;
}
.fill-1 {
    display: flex;
}
.full-aboutme {
    padding: 30px 30px;
    margin-right: auto;
    margin-left: auto;
    background: white;
}

.big-aboutme {
    color: rgba(0, 0,0 ,0.8);
    text-align: center
}
.title {
    margin-bottom: 30px; 
    font-size: 80px; 
    font-weight: 100;
}
.paragraph {
    padding: 20px;
    text-align: left;
    font-size: 20px;
    font-weight: 400; 
}
.container-text {
    height: 100vh; 
    max-height: 70%;
    color: black;
    position: relative;  
    margin: 0 20px; 
}
textarea.ant-input {
    position: relative; 
    height: calc(100vh - 142px) !important; 
    padding: 40px 20px;
    font-weight: 400;
    font-size: 16; 
    line-height: 1.7;
    min-height: 100%;
}
.title {
    font-size: 60px;
    font-weight: 600;
    margin-bottom: 0;
}
.article-title {
    padding-bottom: 20px;
}
.display-title {
    padding-bottom: 52px;
}
.markdown {
    overflow: auto;
}

.main-content{
    padding: 60px 60px;
    background-color: #FAFAFA;
}

.article-container {
    background-color: white;
    padding: 30px 40px !important;
    box-shadow: 8px 10px 18px #888888;;
    color: black;
    
}
.side-bar{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    min-width: 200px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    min-height: 800px;
}
.card-bar {
    padding: 20px; 
    box-shadow: 10px 10px 15px #888888;
    background-color: #f1f9ff;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content; 
    color: black; 
}

.avatar-small {
    height: 100px;
    width: 100px !important;
    border: 1px solid black;
    border-radius: 100px;
    background: url('https://via.placeholder.com/100');
}
.info {
    margin: 10px;
}
.quote {
    margin-top: 20px;
}
.latest-post {
    font-size: 25px;
    font-weight: 200 ; 
}
.title-posts {
    font-weight: 600;
    font-size: 30px;
    padding: 0px 10px;
}
ul > a {
    background-color: transparent;
    color: rgba(122, 118, 118, 0.815) !important;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}

ul > a:hover {
    text-decoration: none;
    color: rgba(79, 134, 216, 0.74) !important;
}
.ant-card-head .ant-card-head-title {
    font-size: 30px;
    font-weight: 700;
    font-family: lora;
}
.ant-card-head {
    padding: 0 0;
}
.ant-card-body {
    font-size: 15px;
    font-weight: 400;
    font-family: poppins;
    color: '#666'; 
    line-height: 1.8;
    padding: 10px 0; 
}
p,a {
    font-family: 'Lato', sans-serif;
}
div > a {
    background-color: transparent;
    color: rgba(0, 0, 0, 0.815) !important;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}

div > a:hover {
    text-decoration: none;
    color: rgba(59, 126, 226, 0.74) !important;
}
